

.radio {
    border: #58ACFF solid 2px;
    margin: 5px;
}

.radio::before {
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 0.25em;
    /*margin-right: 0.5em;*/
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.radio:checked::before {
    opacity: 0.2;
}

.label {
    font-size: 20px;
    color: #212A34;
}

.options {
    margin: 10px;
    border: #212A34 solid 2px;
    border-radius: 5px;
    width: 120px;
}