
.button {
    width: 149px;
    height: 30px;
    color: white;
    border-radius: 3px;
}

.disabled {
    background: #004D99;
    opacity: 0.5;
    border-radius: 3px;
    color: #002E5C;
}

.secondary {
    background: #C9E5FF;
    border: 1px solid #0066CC;
    border-radius: 3px;
    color: #0066CC;
}

.default {
    background-color: #0059B2;
    border: none;
}

.red {
    background: red;
    border: none;
}

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background: #e5f0fa;
}


.default:active {
    background: #0059b3;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b31232;
}

.button:disabled {
    cursor: default;
}
